.slider {
  position: relative;
  width: 270px;

  @include tablet {
    width: 708px;
  }

  @include desktop {
    width: 1200px;
  }
}

.slider__wrapper {
  position: relative;
  display: flex;
  overflow: hidden;
  justify-content: flex-start;

  @include tablet {
    margin: 0 44px;
    &--slider-in-move::before {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      box-shadow: inset 20px 0px $smoky_white,
        inset -20px 0px $smoky_white;
      z-index: 2;
    }
  }

  @include desktop {
    margin: 0 60px;

    &--slider-in-move::before {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      box-shadow: inset 45px 0px $smoky_white,
        inset -45px 0px $smoky_white;
      z-index: 2;
    }
  }


}

.slider__list {
  @extend %reset_default_styles_list;
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
  width: 270px;
  transition: transform 0.6s ease;

  @include tablet {
    width: 620px;
  }

  @include desktop {
    width: 1080px;
  }
}

.slider__item {
  flex-shrink: 0;

  @include tablet {
    padding: 0 20px;
  }

  @include desktop {
    padding: 0 45px;
  }
}

.slider__controls {
  display: flex;
  justify-content: space-between;
  padding: 20px 43px 0;

  @include tablet {
    position: absolute;
    left: 0;
    top: 50%;
    padding: 0;
    width: 100%;
    transform: translate(0, -50%);
    z-index: 3;
  }

}

.slider__button {
  width: 52px;
  height: 52px;
  border: 2px solid $desert_sand;

  &--next {
    transform: rotate(180deg);
  }

  &--active:hover {
    border-color: $apricot_criola;
    background-color: $apricot_criola;
  }
}
